
.header-container {
  position: fixed;
  width: 100%;
  z-index: 100;
}
.top-header {
  height: 20px;
  background: whitesmoke;
  padding: 0 50px;
  text-align: right;
  border-bottom: 1px sold #ccc;
  display: none;
}
.responsive-img {
  height: 110%;
  margin-right: 15px;
}
header.row {
  width: 100%;
  margin: 0;
  padding: 15px 50px;
  background: #fff;
  height: 90px;
  -webkit-box-shadow: 1px 2px 2px rgba(0, 0, 0, 0.2);
          box-shadow: 1px 2px 2px rgba(0, 0, 0, 0.2);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-line-pack: center;
      align-content: center;
  position: relative;
}
header.row:after {
    content: '';
    height: 7px;
    background: #348AFE;
    width: 10%;
    position: absolute;
    bottom: -7px;
    right: 0;
    -webkit-box-shadow: 1px 2px 2px transparentze(#000, 0.8);
            box-shadow: 1px 2px 2px transparentze(#000, 0.8);
}
.brand-name {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 100%;
}
.brand-name a {
    color: #348AFE;
    text-decoration: none;
    -webkit-transition: all ease .5s;
    transition: all ease .5s;
}
.brand-name a:hover {
      color: #9ecfff;
}
.nav-container,
.hamburger-menu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  justify-self: flex-end;
  overflow: hidden;
  -ms-flex-item-align: end;
      align-self: flex-end;
  margin-left: auto;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.hamburger-menu {
  display: none;
}
.main-nav {
  display: table;
  color: #348AFE;
  list-style: none;
  height: 100%;
}
.main-nav li {
    display: table-cell;
    padding: 0 8px;
}
.nav-button {
  color: #3e5871;
  display: block;
  font-size: 22px;
  font-weight: bolder;
  text-decoration: none;
  -webkit-transition: all ease .5s;
  transition: all ease .5s;
}
.nav-button:hover {
    text-decoration: none;
    color: #FE6534;
}
@media (max-width: 768px) {
.top-header {
    display: none;
}
header.row {
    -webkit-box-pack: left;
        -ms-flex-pack: left;
            justify-content: left;
    height: 70px;
    padding-left: 7%;
    padding-right: 7%;
}
.nav-container {
    display: none;
}
.hamburger-menu {
    display: block;
    font-size: 20px;
    font-weight: 900;
}
.brand-name a {
    font-size: 20px;
}
}



